import type { Meta, StoryFn } from '@storybook/web-components';
import { ButtonVariant, DrawerComponentWC } from '@tiger-analytics/ui';
import { html } from 'lit';

export default {
  title: 'Overlay/Drawer/ta-drawer',
  component: 'ta-drawer',
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    id: {
      description: 'Mandatory - unique id for testing',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    visible: {
      description: 'Controls the visibility of the drawer',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: false,
    },
    onClose: {
      description: 'Task to perform on close of the drawer.',
      table: {
        category: 'Custom Events',
      },
      control: false,
    },
    onSubmit: {
      description: 'Task to perform on submit of the drawer.',
      table: {
        category: 'Custom Events',
      },
      control: false,
    },
    goToStep: {
      description:
        'Multi-step only method - This method is available to programmaticaly navigate to a particular slide',
      table: {
        category: 'Methods',
      },
      control: false,
    },
  },
} as Meta;

const description = `The ta-drawer component is used to display sliding drawers, typically from the right side of the screen. It is ideal for scenarios where you need to present additional content or actions without interrupting the main flow. It can support both single-step and multi-step content.

Use it when you need to slide in additional content or actions, such as forms, information panels, or progress steps. The drawer can be controlled to navigate between steps (in the case of multi-step drawers).`;

const Template: StoryFn = (args) => {
  const drawerOpenHandler = (drawerId: string) => {
    const drawer = document.getElementById(drawerId) as HTMLElement;
    drawer.setAttribute('visible', 'true');
  };

  const drawerCloseHandler = (drawerId: string, drawerOpenerId: string) => {
    const drawer = document.getElementById(drawerId) as HTMLElement;
    drawer.removeAttribute('visible');
    const drawerActivateButton = document.getElementById(drawerOpenerId);
    if (drawerActivateButton) {
      drawerActivateButton.focus();
    }
  };

  const programmaticallyNavigateTo1Handler = () => {
    const drawer = document.getElementById(`${args.id}-multi`) as DrawerComponentWC;
    drawer.goToStep(0);
  };

  return html`
    <ta-grid-container .showSideMargin="${false}">
      <ta-grid-column style="gap: 10px">
        <ta-button
          id="activate-drawer-single"
          @click="${() => drawerOpenHandler(`${args.id}-single`)}"
          variant="${ButtonVariant.Secondary}"
          >Show single step drawer</ta-button
        >
        <ta-button
          id="activate-drawer-multi"
          @click="${() => drawerOpenHandler(`${args.id}-multi`)}"
          variant="${ButtonVariant.Secondary}"
          >Show multi step drawer</ta-button
        >
        <ta-drawer
          id="${args.id}-single"
          .visible="${args.visible}"
          @onClose="${() => drawerCloseHandler(`${args.id}-single`, 'activate-drawer-single')}"
          @onSubmit="${() => drawerCloseHandler(`${args.id}-single`, 'activate-drawer-single')}">
          <ta-drawer-slide id="slide-1" title="Slide-1">Step 1 Content </ta-drawer-slide>
        </ta-drawer>
        <ta-drawer
          id="${args.id}-multi"
          .visible="${args.visible}"
          @onClose="${() => drawerCloseHandler(`${args.id}-multi`, 'activate-drawer-multi')}"
          @onSubmit="${() => drawerCloseHandler(`${args.id}-multi`, 'activate-drawer-multi')}">
          <ta-drawer-slide id="slide-1" title="Slide-1">Step 1 Content </ta-drawer-slide>
          <ta-drawer-slide id="slide-2" title="Slide-2"
            ><div>
              <div>Step 2 Content</div>
              <ta-button id="demo-goToStep-method" @click="${programmaticallyNavigateTo1Handler}"
                >programmatically navigate to slide 1</ta-button
              >
            </div></ta-drawer-slide
          >
          <ta-drawer-slide id="slide-3" title="Slide-3">Step 3 Content</ta-drawer-slide>
        </ta-drawer>
      </ta-grid-column>
      <ta-grid-column>
        <div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
      </ta-grid-column>
      <ta-grid-column>
        <div class="story-description">${description}</div>
      </ta-grid-column>
    </ta-grid-container>
  `;
};

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  id: 'test-drawer',
  visible: false,
};
